import React, { useEffect } from "react";
import { Grid, Typography, ImageList, ImageListItem } from "@material-ui/core";
import { useStyles } from "./galleryStyles";
import Aos from "aos";

export default function GridSection({ title, dataSet }) {
  const styles = useStyles();

  useEffect(() => {
    Aos.init({ duration: 2000 });
  }, []);
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      justifyContent="center"
      style={{ marginTop: 60 }}
    >
      <Typography
        variant="h3"
        className={styles.pageTitleStyle}
        data-aos="fade-up"
      >
        {title}
      </Typography>
      <Grid container item xs={null} md={1}></Grid>

      <Grid container item xs={12} md={10}>
        <ImageList
          sx={{ width: 500, height: 450 }}
          variant="woven"
          cols={3}
          gap={16}
          data-aos="fade-up"
        >
          {dataSet.map((item) => (
            <ImageListItem key={item.img}>
              <img
                srcSet={`${item.img}?w=161&fit=crop&auto=format 1x,
                ${item.img}?w=161&fit=crop&auto=format&dpr=2 2x`}
                alt={item.title}
                loading="lazy"
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Grid>
      <Grid container item xs={null} md={1}></Grid>
    </Grid>
  );
}
